import React from 'react';
import './home.css';
import { SearchBar, Switch } from 'antd-mobile'
import HomeCarousel from './HomeCarousel';
import Mytab from './Mytab';
//import Commodity from './Commodity';
//import { data } from 'jquery';

import {connect} from 'react-redux';
import {getgoodslist} from './actionCreators'

const Home = (props) => {
    const { getgoodslist } = props;
    getgoodslist();
   
    return (   
        <div id = "home">
            <div className = "home1">
                <div className = "header1">
                    <i className='iconfont icon-tao' id='tao1'></i>
                </div>

                <div id = "home-search1">
                    <SearchBar
                        placeholder="寻找店铺宝贝"
                        onFocus={() => {
                            props.history.push('/home/detail')
                        }}
                    />                    
                </div>
            </div>

            <div className="home2">
                <HomeCarousel />
            </div>
            
            <div className="bounds">
                <img src='https://img.alicdn.com/tfs/TB1V2eQrKSSBuNjy0FlXXbBpVXa-966-114.png'/>
            </div>

            {/* <div className = "home3" >
                <Commodity {...props}/>
            </div> */}

            <div className="home3">                
            {                       
                props.datalist.map((item,idx)=>{
                    return(
                        <div className="home4" onClick={
                            () =>{props.history.push({ 
                                pathname: "/particulars/detail", 
                                state: `${idx}`})}
                        }>
                            <img src={item.img} alt="#"/>
                            <div className = 'wd' >
                                <p className='tit'>{item.title}</p>
                                <p className='tit2'>
                                    <span className='sp1'>￥</span>
                                    <span className='sp2'>{item.price}</span>
                                    <span className='sp3'>{item.id}</span>
                                    <span className='sp4'>人已购买</span>
                                </p>
                            </div>
                        </div>              
                    )
                })
            }
            </div>              

            <Mytab />
                    
        </div> 
               
    )
}

const mapStateToProps = (state)=>({
    datalist: state.datalist
})

const mapDispatchToProps = (dispatch) => {
    return{
        getgoodslist:()=>dispatch(getgoodslist())
    }
}

export default connect(mapStateToProps,mapDispatchToProps)(Home);


