import React from 'react';
import Mytab from './Mytab';
import { connect } from 'react-redux';
import './cart.css';

const Cart = (props) => {
    return (
        <div className="shopp">
            <div className="shopq">                
            {                       
                props.shoucang.map((item,idx)=>{
                    return(
                        <div className="shop1" key={idx}>
                            <img src={props.datalist[item].img} alt="#" className='shouimg'/>
                            <div className = 'wwd' >
                                <p className='titt1'>{props.datalist[idx].title}</p>
                                <p className='titt2'>
                                    <span className='spt1'>￥</span>
                                    <span className='spt2'>{props.datalist[idx].price}</span>
                                    <span className='spt3'>{props.datalist[idx].id}</span>
                                    <span className='spt4'>人已购买</span>
                                </p>
                            </div>
                            <div className='shan'>
                                <button onClick={()=>{
                                    props.dispatch({
                                        type:'DEL',
                                        index:idx
                                    })
                                }} className="shanbtn">
                                    取消收藏
                                </button>
                            </div>
                        </div>               
                    )
                })
            }
            </div>            
            <Mytab/>
        </div>      
    )
}
const mapStateToProps = (state) => {
    return {
        datalist: state.datalist,
        shoucang: state.shoucang
    }
}
export default connect(mapStateToProps)(Cart)