import React from 'react';
import { Button, TabBar } from 'antd-mobile';
import { withRouter } from 'react-router-dom';
import { connect } from 'react-redux';
class Partab extends React.Component {
    constructor(props) {
        super(props);
        this.state = {
            selectedTab: '',
            status: true
        };
        let shoparr = props.shoucang;
        shoparr.forEach(item => {
            if (item == this.props.location.state) {
                this.state = {
                    selectedTab: 'red',
                    status: false
                };
            }
        });
    }
    render() {
        const pathname = this.props.location.pathname;
        return (       
            <div style={{
                position: 'fixed',
                width: '124px',                
                bottom: 0,
                fontSize:'14px'
            }}>
                <TabBar
                    unselectedTintColor="#949494"
                    tintColor="#FE560A"
                    barTintColor="white"
                >
                    <TabBar.Item
                        title="店铺"
                        key="dianpu"
                        icon={
                            <i className='iconfont icon-dianpu'></i>                           
                        }
                        selectedIcon={
                            <i className='iconfont icon-ziyuan'></i>                           
                        }
                        selected={ this.state.selectedTab === 'yellowTab' }
                        
                        onPress={ () => {
                            this.props.history.push('/shop');
                        } }
                        data-seed="logId"
                    >
                    </TabBar.Item>

                    <TabBar.Item
                        title="客服"
                        key="kefu"
                        icon={
                            <i className='iconfont icon-taobaokefu-01'></i>                           
                        }
                        selectedIcon={
                            <i className='iconfont icon-taobaokefu-01'></i>                           
                        }
                        
                        selected={ pathname === '/server' }
                        onPress={ () => {
                            this.props.history.push('/server');
                        } }
                        data-seed="logId1"
                    >                       
                    </TabBar.Item>

                    <TabBar.Item
                        title="收藏"
                        key="shoucang"
                        icon={
                            <i className='iconfont icon-shoucang'></i>
                        }
                        selectedIcon={
                            <i className='iconfont icon-shoucang1' ></i>                           
                        }  
                        selected={this.state.status == false && this.state.selectedTab === 'red'}
                        onPress={() => {
                            if(this.props.username == ''){
                                this.props.history.push({
                                    pathname:'/logining',
                                })
                            }
                            else{
                                if (this.state.status == true) {
                                    this.props.dispatch({
                                        type: 'ADD',
                                        shoucang: this.props.location.state,
                                    })
                                    this.setState({
                                        selectedTab: 'red',
                                        status: false
                                    });                                   
                                } else {
                                    this.props.dispatch({
                                        type: 'DEL',
                                        index: this.props.location.state
                                    })
                                }
                                
                            }
                        }}    
                                         
                      
                    >    
                    </TabBar.Item>                    
			        			    
                </TabBar>
            </div>
        );
    }
}

const mapStateToProps = (state) => {
    return {
        username: state.login.username,
        shoucang: state.shoucang
    }
}
export default withRouter(connect(mapStateToProps)(Partab))

