import React from 'react';
import { Carousel, WingBlank } from 'antd-mobile';

class HomeCarousel extends React.Component {
  state = {
    data: ['1', '2', '3','4','5','6','7','8'],
    imgHeight: 120,
  }
  componentDidMount() {
    setTimeout(() => {
      this.setState({
        data: [
            'https://gw.alicdn.com/imgextra/i4/148/O1CN01qCo5YT1CxpFVf3soW_!!148-0-lubanu.jpg',
            'https://aecpm.alicdn.com/simba/img/TB1CWf9KpXXXXbuXpXXSutbFXXX.jpg_q50.jpg',
            'https://aecpm.alicdn.com/simba/img/TB15tIjGVXXXXcoapXXSutbFXXX.jpg',
            'https://gw.alicdn.com/imgextra/i1/93/O1CN01A8FWoi1CYdStUW8Dh_!!93-0-lubanu.jpg',
            'https://gw.alicdn.com/imgextra/i2/87/O1CN015w4ZpM1CVt5SJMwF8_!!87-0-lubanu.jpg',
            'https://gw.alicdn.com/imgextra/i2/121/O1CN01dNHOp91ClSYRigyke_!!121-0-lubanu.jpg',
            'https://img.alicdn.com/tps/i4/https://img.alicdn.com/imgextra/i1/6000000006505/O1CN01Oi9jAY1xvL2U5VvVp_!!6000000006505-0-octopus.jpg',
            'https://gw.alicdn.com/imgextra/i2/160/O1CN01jZXP2p1D3K06qncRt_!!160-0-lubanu.jpg'
      
        ],
      });
    }, 100);
  }
  render() {
    return (  
        <Carousel
          autoplay={true}
          infinite
        >
          {this.state.data.map(val => (
            <a
              key={val}
              href="http://www.alipay.com"
              style={{ display: 'inline-block', width: '100%', height: this.state.imgHeight }}
            >
              <img
                src={`${val}`}
                alt=""
                style={{ width: '100%', height:'120px', verticalAlign: 'top', frameOverflow:'hidden'}}
                onLoad={() => {
                  window.dispatchEvent(new Event('resize'));
                  this.setState({ imgHeight: 'auto' });
                }}
              />
            </a>
          ))}
        </Carousel>
    );
  }
}

export default HomeCarousel;




