import React from 'react'
import {login} from './actionCreators';
import {connect} from 'react-redux';
const Login = (props) => {
    const log = ()=>{
        props.dispatch(login());
        props.history.goBack();
    }
    return (
        <div style={{width:'100%',height:'300px',marginTop:'50px'}}>
            <input type="text" aria-label="手机号/邮箱/会员名" placeholder="手机号/邮箱/会员名" style={{width:'280px',height:'30px',float:'left',marginLeft:'50px',marginTop:'20px'}}/>
            <input type="password" aria-label="请输入登录密码" placeholder="请输入登录密码" style={{width:'280px',height:'30px',float:'left',marginLeft:'50px',marginTop:'20px'}}/>
            <button onClick={log} style={{width:'200px',height:'40px',float:'left',marginLeft:'80px',marginTop:'20px',fontSize:'25px'}}>登录</button>
        </div>
    )
}

const mapStateToProps = (state)=>({
    username:state.login.username
})

export default connect(mapStateToProps)(Login);
