let shoucang = [];
const shoucangreducer = (state=shoucang,action) => {
    //添加
    if(action.type == 'ADD'){ 
        //type后面一般跟大写的词（规范问题）
        console.log('reducer',action);
        //不可变对象
        return [...state,action.shoucang]
    }
    //删除，点击删除按钮，绑定下标
    if(action.type == 'DEL'){ 
        //action.index
        let arr = [...state];
        arr.splice(action.index,1);
        return arr;        
    }
    return state;
}

export default shoucangreducer;