import React,{useState,useEffect} from 'react';
import { BrowserRouter as Router, Redirect, Route, NavLink, Switch } from 'react-router-dom'
import Home from './Home';
import Cart from './Cart';
import List from './List';
import My from './My';
import Search from './Search';
import Particulars from './Particulars';
import Shop from './Shop';
import Server from './Server';
import Collect from './Collect';
import Buy from './Buy';
import Login from './Login';
import { Provider } from 'react-redux'
import store from './store';

//淘宝页面
const App = ()=>{
	return <Provider store={store}>
	<Router>
		<Switch>
			<Route exact path='/home' component={Home} />
			<Route path='/cart' component={Cart} />
			<Route path='/list' component={List} />
			<Route path='/my' component={My} />
			<Route path='/home/detail' component={Search} />
			<Route path='/particulars/detail' component={Particulars}/>			
			<Route path='/shop' component={Shop} />
			<Route path='/server' component={Server} />
			<Route path='/collect' component={Collect} />
			<Route path='/buy' component={Buy} />
			<Route path='/logining' component={Login} />
			<Redirect to='/home'/>
			
		</Switch>
		
	</Router>
	</Provider>
}
export default App;