//创建所有的action（都提取出来）
const getgoodslist = () =>{
    return (dispatch)=>{
        let url = 'https://www.fastmock.site/mock/9e4bbf9e35ad15942010865690c87ac6/api/getgoodslist';
        fetch(url,{
            method:'post',
            headers:{
                'Content-type':'application/json'
            },
        })
        .then(res=>res.json())
        .then(res=>{
            dispatch({
                type:'GET-GOOD-LIST',
                datalist: res.data,           
            })          
        })
    }

}
const login = () =>{
    return (dispatch)=>{
        let url = 'https://www.fastmock.site/mock/9e4bbf9e35ad15942010865690c87ac6/api/login';
        fetch(url,{
            method:'post',
            headers:{
                'Content-type':'application/json'
            },
        })
        .then(res=>res.json())
        .then(res=>{
            dispatch({
                type:'LOGIN',
                username:res.data.name,            
            })          
        })
    }
}

export {getgoodslist,login}