import React from 'react';
import { Button, TabBar } from 'antd-mobile';
import { withRouter } from 'react-router-dom';
import { connect } from 'react-redux';
class Mytab extends React.Component {
    constructor(props) {
        super(props);
        this.state = {
            selectedTab: '/home',
        };
    }
    render() {
        const { pathname } = this.props.location;
        return (
            <div style={{
                position: 'fixed',
                width: '100%',
                bottom: 0
            }}>
                <TabBar
                    unselectedTintColor="#949494"
                    tintColor="#FE560A"
                    barTintColor="white"
                >
                    <TabBar.Item
                        title="首页"
                        key="home"
                        icon={
                            <i className='iconfont icon-home2'></i>                           
                        }
                        selectedIcon={
                            <i className='iconfont icon-home3'></i>                           
                        }
                        selected={ pathname === '/home' }
                        onPress={ () => {
                            this.props.history.push('/home');
                        } }
                        data-seed="logId"
                    >
                    </TabBar.Item>

                    <TabBar.Item
                        title="收藏"
                        key="cart"
                        icon={
                            <i className='iconfont icon-shoucang'></i>                           
                        }
                        selectedIcon={
                            <i className='iconfont icon-shoucang1'></i>                           
                        }                       
                        selected={ pathname === '/cart' }
                        onPress={ () => {
                            if (this.props.username == '') {
								this.props.history.push('/logining')
							} else {
								this.props.history.push('/cart');
							}
                        } }
                        data-seed="logId1"
                    >
                    </TabBar.Item>

                    <TabBar.Item
                        title="订单列表"
                        key="list"
                        icon={
                            <i className='iconfont icon-dingdan'></i>
                        }
                        selectedIcon={
                            <i className='iconfont icon-icon'></i>                           
                        }
                        selected={ pathname === '/list' }
                        onPress={ () => {
                            this.props.history.push('/list')
                        } }
                        data-seed="logId2"
                    >                       
			        </TabBar.Item>

                    <TabBar.Item
                        title="我的淘宝"
                        key="my"
                        icon={<i className='iconfont icon-wode1'></i> }
                        selectedIcon={
                            <i className='iconfont icon-wode'></i>                           
                        }
                        selected={ pathname === '/my' }
                        onPress={ () => {
                            this.props.history.push('/my')
                        } }
                        data-seed="logId3"
                    >                        
			        </TabBar.Item>
                </TabBar>
            </div>
        );
    }
}

const mapStateToProps = (state) => {
    return {
        username: state.login.username,
    }
}
export default withRouter(connect(mapStateToProps)(Mytab))

