import React from 'react';
import './particulars.css';
import Partab from './Partab';
import {connect} from 'react-redux';

const Particulars = (props) => {

    return (
        <div className='par'>
            <img src={props.datalist[props.location.state].img} alt="" className='img1'></img>
            <span class='iconfont icon-return' onClick={()=>{props.history.push('/')}} id='fan'></span>
            <div id="xiang">
                <p className='biao1'>{props.datalist[props.location.state].title}</p>
                <p className='biao2'>
                    <span className='spn1'>￥</span>
                    <span className='spn2'>{props.datalist[props.location.state].price}</span>
                    <span className='spn3'>{props.datalist[props.location.state].id}</span>
                    <span className='spn4'>人已购买</span>
                </p>
            </div>
            <div className='par-bottoms'>
                <div className="par-bottom">
                    <div className='par1'>
                        <Partab/>
                    </div>
            
                    <div className='btz1' onClick={()=>props.history.push('/cart')}>
                        <span className='wd1'>加入购物车</span>                    
                    </div>
                    <div className='btz2' onClick={()=>props.history.push('/buy')}>
                        <span className='wd2'>立即购买</span>                    
                    </div>

                </div>
            </div>          
        </div>
    )
}


const mapStateToProps = (state)=>({
    datalist: state.datalist
})

export default connect(mapStateToProps)(Particulars);